"use client";

import { useState, useTransition, useRef, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { Loader2, Sparkles, CornerDownLeft, User, Bot } from 'lucide-react';

import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { getRecommendations } from '@/lib/actions';
import { Form, FormControl, FormField, FormItem } from '@/components/ui/form';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { cn } from '@/lib/utils';
import { Card, CardContent } from '@/components/ui/card';

const chatSchema = z.object({
  message: z.string().min(1, 'Please enter a message.'),
});

type ChatFormData = z.infer<typeof chatSchema>;

type Message = {
  role: 'user' | 'model';
  content: { text: string }[];
};

const exampleQuestions = [
  "How can you help me?",
  "Show me internships for software engineers",
  "I'm interested in marketing and brand strategy",
  "Tell me about internships in New York",
];

export function RecommendationsSection() {
  const { toast } = useToast();
  const [isPending, startTransition] = useTransition();
  const [messages, setMessages] = useState<Message[]>([]);
  const scrollAreaRef = useRef<HTMLDivElement>(null);

  const form = useForm<ChatFormData>({
    resolver: zodResolver(chatSchema),
    defaultValues: { message: '' },
  });

  useEffect(() => {
    if (scrollAreaRef.current) {
      scrollAreaRef.current.scrollTo({
        top: scrollAreaRef.current.scrollHeight,
        behavior: 'smooth',
      });
    }
  }, [messages]);

  const handleSubmitMessage = (messageText: string) => {
    if (!messageText) return;

    const userMessage: Message = { role: 'user', content: [{ text: messageText }] };
    setMessages((prev) => [...prev, userMessage]);
    form.reset();

    startTransition(async () => {
      const result = await getRecommendations(messageText, messages);
      if (result.error) {
        toast({ variant: 'destructive', title: 'Error', description: result.error });
        setMessages((prev) => prev.slice(0, prev.length - 1));
      } else if (result.data) {
        const botMessage: Message = { role: 'model', content: [{ text: result.data.recommendations }] };
        setMessages((prev) => [...prev, botMessage]);
      }
    });
  };

  const onSubmit = (data: ChatFormData) => {
    handleSubmitMessage(data.message);
  };
  
  const handleExampleClick = (question: string) => {
    handleSubmitMessage(question);
  }

  return (
    <div className="mx-auto w-full max-w-4xl space-y-8">
      <div className="flex flex-col items-center justify-center space-y-4 text-center">
        <Sparkles className="h-10 w-10 text-primary" />
        <h2 className="font-headline text-3xl font-bold tracking-tighter sm:text-5xl text-primary">Your Personal Internship Assistant</h2>
        <p className="max-w-[900px] text-muted-foreground md:text-xl/relaxed lg:text-base/relaxed xl:text-xl/relaxed">
          Ask me for internship recommendations, information about specific roles, or how this platform can help you.
        </p>
      </div>

      <Card className="shadow-lg border h-[600px] flex flex-col">
        <CardContent className="p-6 flex-1 flex flex-col">
          <ScrollArea className="flex-1 mb-4 pr-4" ref={scrollAreaRef}>
            <div className="space-y-6">
              {messages.length === 0 && (
                <div className="flex items-start gap-3 justify-start animate-in fade-in-50">
                  <Avatar className="h-8 w-8">
                    <AvatarFallback><Bot /></AvatarFallback>
                  </Avatar>
                  <div className="bg-muted p-3 rounded-lg max-w-lg">
                    <p className="text-sm">Hello! I'm the IamHR assistant. I can help you find personalized internship recommendations or answer your questions about opportunities on this platform. What are you looking for?</p>
                  </div>
                </div>
              )}
              {messages.map((message, index) => (
                <div
                  key={index}
                  className={cn(
                    'flex items-start gap-3',
                    message.role === 'user' ? 'justify-end' : 'justify-start'
                  )}
                >
                  {message.role === 'model' && (
                    <Avatar className="h-8 w-8 shrink-0">
                      <AvatarFallback><Bot /></AvatarFallback>
                    </Avatar>
                  )}
                  <div
                    className={cn(
                      'max-w-xs md:max-w-md lg:max-w-2xl p-3 rounded-lg',
                      message.role === 'user'
                        ? 'bg-primary text-primary-foreground'
                        : 'bg-muted'
                    )}
                  >
                    <div className="text-sm prose prose-sm max-w-none" dangerouslySetInnerHTML={{ __html: message.content[0].text.replace(/\n/g, '<br />') }} />
                  </div>
                  {message.role === 'user' && (
                     <Avatar className="h-8 w-8 shrink-0">
                       <AvatarFallback><User /></AvatarFallback>
                     </Avatar>
                  )}
                </div>
              ))}
              {isPending && (
                 <div className="flex items-start gap-3 justify-start">
                    <Avatar className="h-8 w-8"><AvatarFallback><Bot /></AvatarFallback></Avatar>
                    <div className="bg-muted p-3 rounded-lg"><Loader2 className="h-5 w-5 animate-spin" /></div>
                 </div>
              )}
            </div>
          </ScrollArea>
           <div className="pt-4 border-t">
            {messages.length < 2 && (
              <div className="mb-4 space-y-2">
                <div className='flex items-center gap-2 text-sm text-muted-foreground'>
                  <Sparkles className="h-4 w-4" />
                  <p>Try an example:</p>
                </div>
                <div className="flex flex-wrap gap-2">
                  {exampleQuestions.map((q) => (
                    <Button key={q} variant="outline" size="sm" onClick={() => handleExampleClick(q)} disabled={isPending}>
                      {q}
                    </Button>
                  ))}
                </div>
              </div>
            )}
            <Form {...form}>
              <form onSubmit={form.handleSubmit(onSubmit)} className="flex items-center gap-2">
                <FormField
                  control={form.control}
                  name="message"
                  render={({ field }) => (
                    <FormItem className="flex-1">
                      <FormControl>
                        <Input
                          placeholder="Ask me anything about internships..."
                          {...field}
                          disabled={isPending}
                        />
                      </FormControl>
                    </FormItem>
                  )}
                />
                <Button type="submit" size="icon" disabled={isPending}>
                  {isPending ? <Loader2 className="h-4 w-4 animate-spin" /> : <CornerDownLeft className="h-4 w-4" />}
                </Button>
              </form>
            </Form>
           </div>
        </CardContent>
      </Card>
    </div>
  );
}
